import { useMemo } from "react";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalanceToken } from "../../utils/formatBalance";
import useGetTokensForSale from "../../hooks/state/useGetTokensForSale";
import useGetTokensAllocated from "../../hooks/state/useGetTokensAllocated";

const TokenAllocationNumbersInToken = (props: ITokenSymbolProp) => {
  const core = useCore();
  const tokensForSale = useGetTokensForSale(props.tokenSymbol);
  const tokensAllocated = useGetTokensAllocated(props.tokenSymbol);

  const tokensAllocaltedFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(tokensAllocated.value, core.tokens[props.tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 });
  }, [tokensAllocated, core, props.tokenSymbol]);

  const tokensForSaleFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(tokensForSale.value, core.tokens[props.tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [tokensForSale, core, props.tokenSymbol]);

  return (
    <div>
      {
        tokensForSale.isLoading || tokensAllocated.isLoading
          ? <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
          : (
            <DataField
              label={`${tokensAllocaltedFormatted} / ${tokensForSaleFormatted} ${props.tokenSymbol.toUpperCase()}`}
              labelFontSize={12}
              labelFontColor={theme.color.transparent[100]}
            />
          )
      }
    </div>
  );
}

export default TokenAllocationNumbersInToken;
