
import { useMemo } from "react";
import { BigNumber } from "ethers";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import ProgressBar from "../ProgressBar";

import { ITokenSymbolProp } from "../../utils/interface";
import useGetTokensForSale from "../../hooks/state/useGetTokensForSale";
import useGetTokensAllocated from "../../hooks/state/useGetTokensAllocated";
import { getDisplayBalance } from "../../utils/formatBalance";

const PercentCompleted = (props: ITokenSymbolProp) => {
  const tokensForSale = useGetTokensForSale(props.tokenSymbol);
  const tokensAllocated = useGetTokensAllocated(props.tokenSymbol);

  const percentCompleted = useMemo(() => {
    if (tokensAllocated.value.gte(tokensForSale.value)) return BigNumber.from(1e6).mul(100);
    return tokensAllocated.value
      .mul(1e6)
      .mul(100)
      .div(tokensForSale.value)
  }, [tokensAllocated, tokensForSale]);

  return (
    <div>
      {
        tokensAllocated.isLoading || tokensForSale.isLoading
          ? <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
          : (
            <div className="m-b-4">
              <ProgressBar
                barColor={theme.color.primary[400]}
                percent={
                  Number(getDisplayBalance(percentCompleted, 6, 4))
                }
                width={'100%'}
              />
            </div>
          )
      }
    </div>
  );
}

export default PercentCompleted;
