import { useWallet } from "use-wallet";
import { useEffect, useMemo, useState } from "react";

import Ended from "./Ended";
import Started from "./Started";
import Upcoming from "./Upcoming";
import IDOCountdown from "./IDOCountdown";

import callApi from "../../../utils/apiCaller";
import { IProjectIdProp, ITokenSymbolProp } from "../../../utils/interface";
import useGetStartAndEndDate from "../../../hooks/state/useGetStartAndEndDate";

export interface IuserStatus {
  isLoading: boolean;
  value: "Not Registered" | "Registered";
}

export const initUserStatus: IuserStatus = {
  isLoading: true,
  value: "Registered",
}

export interface IkycStatus {
  isLoading: boolean;
  value: 'incomplete' | 'waiting' | 'approved' | 'inreview' | 'rejected';
}

export const initKycStatus: IkycStatus = {
  isLoading: true,
  value: "incomplete",
}

const RightSection = (props: ITokenSymbolProp & IProjectIdProp) => {
  const {
    tokenSymbol,
    id
  } = props;

  const [userStatus, setUserStatus] = useState<IuserStatus>(initUserStatus);
  const [kycStatus, setKycStatus] = useState<IkycStatus>(initKycStatus);
  const [userEmail, setUserEmail] = useState<string>("");

  const { account } = useWallet();
  const startAndEndDate = useGetStartAndEndDate(props.tokenSymbol);

  const getKYCUserStat = async () => {
    await callApi("kyc/project/status", "POST", {
      walletAddress: account,
      projectId: id,
    })
      .then((data) => {
        console.log("kyc/project/status", data);
        setUserEmail(data.email)
        setUserStatus({
          isLoading: false,
          value: data.userStatus,
        });
        setKycStatus({
          isLoading: false,
          value: data.usersKycStatus,
        });
      })
      .catch((error) => {
        setUserStatus(initUserStatus);
        setKycStatus(initKycStatus);
        console.log("kyc/project/status errror", error);
      });
  }

  useEffect(() => {
    if (account) {
      setKycStatus(initKycStatus);
      setUserStatus(initUserStatus);
      getKYCUserStat();
    }
    const intervals = setInterval(() => {
      getKYCUserStat();
    }, 5000);

    return () => clearInterval(intervals);

  }, [account, id]);

  const status: string = useMemo(() => {
    return "upcoming";
    if (startAndEndDate.startDate * 1000 > Date.now()) return "upcoming";
    if (Date.now() < startAndEndDate.endDate * 1000) return "live";
    return "closed";
  }, [startAndEndDate]);

  // if (startAndEndDate.isLoading) return null

  return (
    <div>
      <IDOCountdown status={status} tokenSymbol={tokenSymbol} id={id} startAndEndDate={startAndEndDate}/>
      <Upcoming
        id={id}
        status={status}
        tokenSymbol={tokenSymbol}
        userStatus={userStatus}
        kycStatus={kycStatus}
        email={userEmail}
      />
      <Started status={status} id={id.toUpperCase()} tokenSymbol={tokenSymbol} />
      <Ended status={status} tokenSymbol={tokenSymbol} />
    </div>
  );
}

export default RightSection;
