import { useMemo } from "react";
import { BigNumber } from "@ethersproject/bignumber";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";
import useGetMinAndMaxAllocation from "../../hooks/state/useGetMinAndMaxAllocation";

const MinAndMaxAllocation = (props: ITokenSymbolProp) => {
  const { tokenSymbol } = props;

  const core = useCore();
  const tradeValue = useGetTokenSalePrice(tokenSymbol);
  const minAndMaxAllocation = useGetMinAndMaxAllocation(tokenSymbol);

  const minAllocationFormatted = useMemo(() => {
    const value = minAndMaxAllocation.minAllocation
      .mul(tradeValue.value)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    return Number(getDisplayBalance(value, 18, 3)).toLocaleString(
      'en-US',
      { minimumFractionDigits: 2, maximumFractionDigits: 2 }
    );
  }, [minAndMaxAllocation, core, tradeValue, tokenSymbol]);

  const maxAllocationFormatted = useMemo(() => {
    const value = minAndMaxAllocation.maxAllocation
      .mul(tradeValue.value)
      .div(BigNumber.from(10).pow(core.tokens[tokenSymbol].decimal));

    return Number(getDisplayBalance(value, 18, 3)).toLocaleString(
      'en-US',
      { minimumFractionDigits: 2, maximumFractionDigits: 2 }
    );
  }, [minAndMaxAllocation, core, tradeValue, tokenSymbol]);

  return (
    <div>
     {/* <DataField
        label={"Min. Allocation"}
        value={minAllocationFormatted + ' BNB'}
        isValueLoading={minAndMaxAllocation.isLoading || tradeValue.isLoading}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />
      <DataField
        label={"Max. Allocation"}
        value={maxAllocationFormatted + ' BNB'}
        isValueLoading={minAndMaxAllocation.isLoading || tradeValue.isLoading}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />*/}
      <DataField
        label={"Min. Allocation"}
        value={0.00 + ' BNB'}
        isValueLoading={false}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />
      <DataField
        label={"Max. Allocation"}
        value={1488.63 + ' BNB'}
        isValueLoading={false}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />
    </div>
  );
}

export default MinAndMaxAllocation;
