import { useMemo } from "react";
import Loader from "react-spinners/BeatLoader";

import theme from "../../theme";

import DataField from "../DataField";
import TextWrapper from "../TextWrapper";

import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTokenSalePrice from "../../hooks/state/useGetTokenSalePrice";

interface IProps {
  isDataField?: boolean;
}

const TokenPrice = (props: ITokenSymbolProp & IProps) => {
  const { isDataField = false, tokenSymbol } = props;

  const tradeValue = useGetTokenSalePrice(tokenSymbol);

  const tokenPriceInBNBFormatted = useMemo(() => {
    return Number(getDisplayBalance(tradeValue.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 6 });
  }, [tradeValue]);

  if (isDataField) return (
    <DataField
      label={`${tokenSymbol.toUpperCase()} Price`}
      value={tokenPriceInBNBFormatted + ' BNB'}
      isValueLoading={tradeValue.isLoading}
      labelFontSize={18}
      labelFontColor={theme.color.transparent[100]}
      valueFontSize={18}
      valueFontColor={"white"}
      valueFontWeight={600}
      className="m-b-16"
    />
  );

  return (
    <div>
      {/*{
        tradeValue.isLoading
          ? <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
          : <TextWrapper
            text={
              `1 ${tokenSymbol.toUpperCase()} = ${tokenPriceInBNBFormatted} BNB`
            }
            fontSize={12}
            fontWeight={600}
            Fcolor={theme.color.transparent[100]}
          />
      }*/}
      <TextWrapper
        text={
          `1 SCLP = 0.11909 BNB`
        }
        fontSize={12}
        fontWeight={600}
        Fcolor={theme.color.transparent[100]}
      />

    </div>
  );
}

export default TokenPrice;
