import React, { useMemo, useState } from "react";

import KycModal from "../modal/KycModal";
import IDOOn from '../../../components/IDOOn';
import Button from "../../../components/Button";
import InfoTip from "../../../components/InfoTip";
import TotalRaised from '../../../components/TotalRaised';
import { IkycStatus, IuserStatus } from "./ProjectStatsSection";
import MinAndMaxAllocation from '../../../components/MinAndMaxAllocation';

import useGetIsWhitelisted from "../../../hooks/state/useGetIsWhitelisted";
import useGetStartAndEndDate from "../../../hooks/state/useGetStartAndEndDate";
import IsNotWhitelistedStatic from "../../../components/IsNotWhitelistedStatic";
import { IProjectIdProp, IStatusProps, ITokenSymbolProp } from "../../../utils/interface";

interface IProps {
  userStatus: IuserStatus;
  kycStatus: IkycStatus;
  email: string;
}

const Upcoming = (props: IProps & IStatusProps & ITokenSymbolProp & IProjectIdProp) => {
  const {
    tokenSymbol,
    userStatus,
    kycStatus,
    id,
    status,
    email,
  } = props;

  const [openRegistration, setOpenRegistration] = useState<boolean>(false);

  const isWhitelisted = useGetIsWhitelisted(tokenSymbol);

  const formLink = "https://docs.google.com/forms/d/e/1FAIpQLSf44fwdJanp44xFeJvc7GLZ16iI2fLGQTvwZH7vOF7cBU6avA/viewform"

  const RegisterButton = useMemo(() => {
    if (userStatus.isLoading || kycStatus.isLoading || isWhitelisted.isLoading || isWhitelisted.value) return null

    if (userStatus.value === "Not Registered") {
      return (
        <div className="m-t-8">
          <Button
            onClick={() => window.open(formLink, "_blank")}
          >
            Register
          </Button>
        </div>

      )
    }

    /*if (kycStatus.value === "incomplete") {
      return (
        <div className="m-t-8">
          <Button
            onClick={() => setOpenRegistration(true)}
          >
            Complete KYC
          </Button>
        </div>

      )
    } else if (kycStatus.value === "approved" || kycStatus.value === "rejected") {
      return null
    } else {
      return (
        <div className="m-t-8">
          <Button
            disabled={true}
          >
            Complete KYC
          </Button>
        </div>

      )
    }*/

    return null;
  }, [userStatus, kycStatus])

  const KycStatus = useMemo(() => {
    if (kycStatus.isLoading || userStatus.isLoading || userStatus.value === "Not Registered") return null
    if (!kycStatus.isLoading) {
      if (isWhitelisted.value) {
        return <InfoTip type={"Success"} msg={"Congratulations! You are whitelisted!"} />
      }

      if (kycStatus.value === "incomplete") {
        return null
        return <InfoTip type={"Warning"} msg={"Your KYC process is incomplete. Please complete your KYC to participate " +
        "in the Whitelisting competition."} />
      } else if (kycStatus.value === "rejected") {
        return <InfoTip type={"Error"} msg={"Your KYC is rejected. Please read out T&C."} />
      } else if (kycStatus.value === "waiting") {
        return null
        return <InfoTip type={"Warning"} msg={"Your KYC is in process. Please come back later to check the status"} />
      } else if (kycStatus.value === "inreview") {
        return null
        return <InfoTip type={"Info"} msg={"Your KYC is in process. Please come back later to check the status"} />
      } else if (kycStatus.value === "approved") {
        return <InfoTip type={"Success"} msg={"Congrats! Your KYC is verified."} />
      }
    }
    return null
  }, [kycStatus, userStatus]);

  const WhiteListedStatus = useMemo(() => {
    if (!isWhitelisted.value) {
      return <IsNotWhitelistedStatic />;
    }
  }, [isWhitelisted])

  if (status !== "upcoming") return <></>;

  return (
    <div className={"material-primary m-b-24"}>
      <KycModal
        onModalClose={() => setOpenRegistration(false)}
        onModalOpen={() => setOpenRegistration(true)}
        modalOpen={openRegistration}
        userStatus={userStatus}
        id={id}
        email={email}
      />
      <div>
        <TotalRaised tokenSymbol={tokenSymbol} />
        <MinAndMaxAllocation tokenSymbol={tokenSymbol} />
        <IDOOn tokenSymbol={tokenSymbol} format={'MMM Do YYYY, h:mm A'} />
      </div>
      <div className="m-t-32">
        {KycStatus}
        <div className="m-t-8">
          <Button
            onClick={() => window.open(formLink, "_blank")}
          >
            Register
          </Button>
        </div>
        {/*{RegisterButton}*/}
      </div>
    </div>
  );
}

export default Upcoming;
