import { useMemo, useState } from "react";

import theme from "../../../theme";

import Button from "../../../components/Button";
import InfoTip from "../../../components/InfoTip";
import Divider from "../../../components/Divider";
import DataField from "../../../components/DataField";
import TokenPrice from "../../../components/TokenPrice";
import TotalRaised from "../../../components/TotalRaised";
import AmountRaised from "../../../components/AmountRaised";
import TotalAllocation from "../../../components/TotalAllocation";
import PercentCompleted from "../../../components/PercentCompleted";

import useCore from "../../../hooks/useCore";
import SuccessModal from "../../../components/SuccesModal";
import useRedeem from "../../../hooks/callbacks/useRedeem";
import { getDisplayBalanceToken } from "../../../utils/formatBalance";
import { IStatusProps, ITokenSymbolProp } from "../../../utils/interface";
import useGetMyAllocations from "../../../hooks/state/useGetMyAllocations";
import useGetClaimedAllocations from "../../../hooks/state/useGetClaimedAllocations";
import useGetClaimableAllocationts from "../../../hooks/state/useGetClaimableAllocations";
import TokenAllocationNumbersInToken from "../../../components/TokenAllocationNumbersInToken";

const Ended = ({ tokenSymbol, status }: ITokenSymbolProp & IStatusProps) => {
  const [miningModal, setMiningModal] = useState<boolean>(false);
  const [successModal, setSuccessModal] = useState<boolean>(false);

  const core = useCore();
  const myAllocation = useGetMyAllocations(tokenSymbol);
  const claimedAllocation = useGetClaimedAllocations(tokenSymbol);
  const claimableAllocations = useGetClaimableAllocationts(tokenSymbol);

  const claimHandler = useRedeem(
    tokenSymbol,
    myAllocation.value
  );

  const yourRedeemableAmountFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(myAllocation.value, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [myAllocation, core, tokenSymbol]);

  const yourClaimedAmountFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(claimedAllocation.value, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [claimedAllocation, core, tokenSymbol]);

  const txIsMininingModalCb = () => {
    setMiningModal(true);
    const timeout = setTimeout(() => setMiningModal(false), core.config.modalCloseIntervals);
    return () => clearTimeout(timeout);
  }

  const txIsSuccessfullCb = () => {
    setSuccessModal(false);
    setSuccessModal(true);
    const timeout = setTimeout(() => setSuccessModal(false), core.config.modalCloseIntervals);
    return () => clearTimeout(timeout);
  }

  const onClaimClick = () => {
    claimHandler(txIsMininingModalCb, txIsSuccessfullCb);
  };

  if (status !== "closed") return <></>;

  return (
    <div className={"material-primary m-b-24"}>
      <SuccessModal
        modalOpen={miningModal}
        setModalOpen={() => setMiningModal(false)}
        title={"Tx submitted! Your transaction is being mined on the blockchain"}
      />
      <SuccessModal
        modalOpen={successModal}
        setModalOpen={() => setSuccessModal(false)}
        title={"Congratulations! You have successfully contributed in the Scallop sale."}
      />
      <div>
        <div className={"m-b-16"}>
          <AmountRaised tokenSymbol={tokenSymbol} />
          <PercentCompleted tokenSymbol={tokenSymbol} />
          <TokenAllocationNumbersInToken tokenSymbol={tokenSymbol} />
        </div>
        <TotalRaised tokenSymbol={tokenSymbol} />
        <TokenPrice isDataField={true} tokenSymbol={tokenSymbol} />
        <Divider />
        <TotalAllocation tokenSymbol={tokenSymbol} />
        <DataField
          label={"Claimable"}
          value={`${yourRedeemableAmountFormatted} ${tokenSymbol}`}
          labelFontSize={18}
          labelFontColor={theme.color.transparent[100]}
          valueFontSize={18}
          valueFontColor={"white"}
          valueFontWeight={600}
          className={"m-b-16"}
        />
        <DataField
          label={"Claimed"}
          value={`${yourClaimedAmountFormatted} ${tokenSymbol}`}
          labelFontSize={18}
          labelFontColor={theme.color.transparent[100]}
          valueFontSize={18}
          valueFontColor={"white"}
          valueFontWeight={600}
        />
        <div className="m-t-32">
          <InfoTip type={"Warning"} msg={"The sale has ended."} />
        </div>
        {
          myAllocation.value.lte(0) && (
            <div className="m-t-12">
              <InfoTip type={"Warning"} msg={"You have no allocation to claim."} />
            </div>
          )
        }
        {
          myAllocation.value.gt(0) && claimableAllocations.value.lte(0) && (
            <div className="m-t-12">
              <InfoTip type={"Warning"} msg={"You have already claimed your total allocation."} />
            </div>
          )
        }
        <div className="m-t-32">
          <Button
            onClick={onClaimClick}
            disabled={myAllocation.value.lte(0) || claimableAllocations.value.lte(0)}>
            Claim {tokenSymbol}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default Ended;
