import { useWallet } from "use-wallet";
import styled from "styled-components";
import Grid from "@material-ui/core/Grid";
import React, { useEffect, useState } from "react";

import theme from "../../../theme";
import '../../../customCss/steps.css';

import Modal from "../../../components/Modal";
import Button from "../../../components/Button";
import KycProcess from "../components/KycProcess";
import InfoTip from "../../../components/InfoTip";
import DataField from "../../../components/DataField";
import { CustomInput } from "../../../components/Input";
import { IuserStatus } from "../components/ProjectStatsSection";
import { States, StatesMsg } from "../../../components/StatesBorder";

import callApi from "../../../utils/apiCaller";
import { IProjectIdProp } from "../../../utils/interface";
import TextWrapper from "../../../components/TextWrapper";

interface Iprops {
  modalOpen: boolean;
  onModalOpen: () => void;
  onModalClose: () => void;
  userStatus: IuserStatus;
  email: string;
}

interface inputFields {
  value: string;
  errorStatus: 'error' | 'warning' | 'default';
  errorMsg: string;
}

const initInputFields: inputFields = {
  value: "",
  errorStatus: 'default',
  errorMsg: "",
}

const KycModal = (props: Iprops & IProjectIdProp) => {
  const {
    modalOpen,
    onModalClose,
    userStatus,
    id,
    email = "",
  } = props;

  const [name, setName] = useState<inputFields>(initInputFields);
  const [emailAddress, setEmailAddress] = useState<inputFields>(initInputFields);
  const [teleId, setTeleId] = useState<inputFields>(initInputFields);
  const [twitterId, setTwitterId] = useState<inputFields>(initInputFields);
  const [walletAddress, setWalletAddress] = useState<inputFields>(initInputFields);
  const [sendingData, setSendingData] = useState<boolean>(false);

  const [activeStep, setActiveStep] = useState<number>(0);
  const [completed, setCompleted] = React.useState(new Set());

  const { account } = useWallet();

  useEffect(() => {
    if (email && email !== "")
      setEmailAddress({
        value: email,
        errorStatus: 'default',
        errorMsg: "",
      })
  }, [email])

  useEffect(() => {
    if (userStatus.value === "Registered") setActiveStep(1)
    else setActiveStep(0);
  }, [userStatus, account])

  useEffect(() => {
    ResetFields()
    setWalletAddress({
      value: account ? account : "",
      errorStatus: 'default',
      errorMsg: "",
    })
  }, [account])

  const ResetFields = () => {
    setName(initInputFields);
    setEmailAddress(initInputFields);
    setTwitterId(initInputFields);
    setTeleId(initInputFields);
  }

  const validate = () => {
    let valid = true
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    if (name.value === "") {
      setName({
        ...name,
        errorStatus: "error",
        errorMsg: "This field is required",
      })
      valid = false;
    }
    if (emailAddress.value === "") {
      setEmailAddress({
        ...emailAddress,
        errorStatus: "error",
        errorMsg: "This field is required",
      })
      valid = false;
    } else if (!re.test(String(emailAddress.value).toLowerCase())) {
      setEmailAddress({
        ...emailAddress,
        errorStatus: "error",
        errorMsg: "Invalid Email Id",
      })
      valid = false;
    }

    /*if (walletAddress.value === "") {
      setWalletAddress({
        ...walletAddress,
        errorStatus: "error",
        errorMsg: "This field is required",
      })
      valid = false
    }*/
    return valid;
  }

  const onSubmit = () => {
    if (validate()) {
      setSendingData(true)
      callApi("user", "post", {
        name: name.value,
        email: emailAddress.value,
        telegramId: teleId.value,
        twitterId: twitterId.value,
        walletAddress: account,
        projectId: id,
      })
        .then((data) => {
          console.log('data', data, {
            name: name.value,
            email: emailAddress.value,
            telegramId: teleId.value,
            twitterId: twitterId.value,
            walletAddress: account,
            projectId: id,
          });
          setSendingData(false)
          if (data?.success || data?.userStatus === "Registered") {
            handleStep();
          }
        })
        .catch((err) => {
          setSendingData(false)
          alert('Internal Server Error');
        })
    };
  }

  function getSteps() {
    return ['Registration', 'Complete KYC'];
  }

  function getStepContent(step: number) {
    switch (step) {
      case 0:
        return step1();
      case 1:
        return step2();
      default:
        return step1();
    }
  }

  const step1 = () => {
    return (
      <div>
        <div className="m-b-16">
          <DataField
            label={"Full Name"}

            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={name.errorStatus === "error" ? "error" : "default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"John"}
                value={name.value}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setName({
                    value: event.target.value,
                    errorStatus: 'default',
                    errorMsg: "",
                  });
                }}
              />
            </InputContainer>
          </States>
          {name.errorStatus === "error" && <StatesMsg state={"error"}>
            {name.errorMsg}
          </StatesMsg>}
        </div>
        <div className="m-b-16">
          <DataField
            label={"Email Address"}

            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={emailAddress.errorStatus === "error" ? "error" : "default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"john@xyz.com"}
                value={emailAddress.value}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setEmailAddress({
                    value: event.target.value,
                    errorStatus: 'default',
                    errorMsg: "",
                  });
                }}
              />
            </InputContainer>
          </States>
          {emailAddress.errorStatus === "error" && <StatesMsg state={"error"}>
            {emailAddress.errorMsg}
          </StatesMsg>}
        </div>
        <div className="m-b-16">
          <DataField
            label={"Telegram ID (optional)"}

            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={"default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"@john"}
                value={teleId.value}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setTeleId({
                    value: event.target.value,
                    errorStatus: 'default',
                    errorMsg: "",
                  });
                }}
              />
            </InputContainer>
          </States>
          {teleId.errorStatus === "error" && <StatesMsg state={"error"}>
            {teleId.errorMsg}
          </StatesMsg>}
        </div>
        <div className="m-b-16">
          <DataField
            label={"Twitter ID (optional)"}

            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={"default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"@john"}
                value={twitterId.value}
                disabled={false}
                type={'string'}
                onChange={(event) => {
                  setTwitterId({
                    value: event.target.value,
                    errorStatus: 'default',
                    errorMsg: "",
                  });
                }}
              />
            </InputContainer>
          </States>
        </div>
        <div className="m-b-8">
          <DataField
            label={"Wallet Address"}
            labelFontColor={theme.color.transparent[100]}
            className={"m-b-8"}
          />
          <States state={walletAddress.errorStatus === "error" ? "error" : "default"}>
            <InputContainer>
              <CustomInput
                inputMode={"text"}
                placeholder={"0xE249b8015b537679eF858C077BA78d2EF7578a58"}
                value={walletAddress.value}
                disabled={true}
                type={'string'}
                onChange={(event) => {
                  setWalletAddress({
                    value: event.target.value,
                    errorStatus: 'default',
                    errorMsg: "",
                  });
                }}
              />
            </InputContainer>
          </States>
          {walletAddress.errorStatus === "error" && <StatesMsg state={"error"}>
            {walletAddress.errorMsg}
          </StatesMsg>}
          <InfoTip
            type={"Warning"}
            msg={"Wallet address is auto filled from metamask."}
            className="m-t-8"
          />
        </div>
        <InfoTip
          type={"Info"}
          msg={"Please check the required details before proceeding further"}
          className="m-b-8"
        />
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => onModalClose()}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              loading={sendingData}
              onClick={() => onSubmit()}
            >
              Register
            </Button>
          </Grid>
        </Grid>
      </div>
    );
  }

  const step2 = () => {
    return (
      <div>
        <TextWrapper
          text={"Please note that after completing the Continue with KYC process, " +
          "you will get an update of the KYC status\n" +
          "on your given email address."}
          fontSize={16}
        />
        <div className="m-t-32">
          <InfoTip
            type={"Info"}
            msg={"Please come back here to check your KYC Status."}
            className="m-b-8"
          />
          <Grid container spacing={2}>
            {/*<Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={"transparent"}
                onClick={() => handleStepBack()}
                disabled={userStatus.value === "Registered"}
              >
                Back
              </Button>
            </Grid>*/}
            <Grid item lg={12} md={12} sm={12} xs={12}>
              <Button
                onClick={() => {
                  if (emailAddress.value) window.open(`/#/projects/${id}/kyc-verification?email=${emailAddress.value}`, "_blank")
                  else window.open(`/#/projects/${id}/kyc-verification`, "_blank")
                  onModalClose()
                }}
              >
                Continue with KYC
              </Button>
              {/*<KycProcess
                walletAddress={walletAddress.value}
                id={id}
                onProcessStart={() => {
                  console.log("onProcessStart")
                  onModalClose()
                }}
                onProcessCancel={() => {
                  console.log("onProcessCancel")
                  onModalClose()
                }}
                onProcessClose={() => {
                  console.log("onProcessClose")
                  onModalClose()
                }}
                onProcessSuccess={() => {
                  console.log("onProcessSuccess")
                  onModalClose()
                }}
              />*/}
            </Grid>
          </Grid>
        </div>
      </div>
    );
  }

  const handleStep = () => {
    setActiveStep(activeStep + 1);
    const newCompleted = new Set(completed);
    newCompleted.add(activeStep);
    setCompleted(newCompleted);
  }

  const handleStepBack = () => {
    setActiveStep(activeStep - 1);
    const newCompleted = new Set(completed);
    newCompleted.delete(activeStep);
    setCompleted(newCompleted);
  }

  const steps = getSteps();
  const PageBar = steps.map((item: string, index: number) => {
    return (
      <div className={`${index === activeStep ? "step-active" : "step-inactive"}`}>
        <span className={`${index === activeStep ? "step-active-text" : "step-inactive-text"}`}>{item}</span>
      </div>
    );
  });

  return (
    <Modal
      title={"Register"}
      closeButton
      open={modalOpen}
      handleClose={() => onModalClose()}
    >
      <MainBar>{PageBar}</MainBar>
      {getStepContent(activeStep)}
    </Modal>
  );
}

export default KycModal;

const InputContainer = styled.div`
  background: ${theme.color.dark[500]};
  border-radius: 6px;
`;

const MainBar = styled.div`
  display: flex;
  flex-direction: row;
  border-radius:10px;
  background: rgba(255, 255, 255, 0.08);
  height: 24px;
  margin-bottom: 24px;
`;

const RulesList = styled.ol`
  padding-left: 20px;
  color: white;
  list-style-type: decimal;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.88);
`;
