import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";
import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalance } from "../../utils/formatBalance";
import useGetTotalRaiseCost from "../../hooks/state/useGetTotalRaiseCost";

const TotalRaised = (props: ITokenSymbolProp) => {
  const totalRaiseCost = useGetTotalRaiseCost(props.tokenSymbol);

  const formattedValue: string = useMemo(() => {
    return Number(getDisplayBalance(totalRaiseCost.value, 18, 6))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [totalRaiseCost]);

  return (
    <div>
      {/*<DataField
        label={"Total raise"}
        isValueLoading={totalRaiseCost.isLoading}
        value={`${formattedValue} BNB`}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />*/}
      <DataField
        label={"Total raise"}
        isValueLoading={false}
        value={`1488.63 BNB`}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className="m-b-16"
      />
    </div>
  );
}

export default TotalRaised;
