import React, { useState } from "react";

import InvestModal from "../modal/InvestModal";
import Button from "../../../components/Button";
import Divider from "../../../components/Divider";
import TokenPrice from "../../../components/TokenPrice";
import TotalRaised from "../../../components/TotalRaised";
import SuccessModal from "../../../components/SuccesModal";
import AmountRaised from "../../../components/AmountRaised";
import TotalAllocation from "../../../components/TotalAllocation";
import PercentCompleted from "../../../components/PercentCompleted";
import MinAndMaxAllocation from "../../../components/MinAndMaxAllocation";
import IsNotWhitelistedStatic from "../../../components/IsNotWhitelistedStatic";
import TokenAllocationNumbersInToken from "../../../components/TokenAllocationNumbersInToken";

import useGetIsWhitelisted from "../../../hooks/state/useGetIsWhitelisted";
import useGetMyAllocations from "../../../hooks/state/useGetMyAllocations";
import useGetAvailableTokens from "../../../hooks/state/useGetAvailableTokens";
import {
  IProjectIdProp,
  IStatusProps,
  ITokenSymbolProp
} from "../../../utils/interface";

const Started = ({ tokenSymbol, id, status }: ITokenSymbolProp & IStatusProps & IProjectIdProp) => {
  const [miningModal, setMiningModal] = useState<boolean>(false);
  const [successModal, setSuccessModal] = useState<boolean>(false);
  const [openInvestModal, setOpenInvestModal] = useState<boolean>(false);

  const myAllocation = useGetMyAllocations(tokenSymbol);
  const isWhitelisted = useGetIsWhitelisted(tokenSymbol);
  const availableTokens = useGetAvailableTokens(tokenSymbol);

  if (status !== "live") return <></>;


  return (
    <div className={"material-primary m-b-24"}>
      <InvestModal
        id={id}
        onMiningClose={() => setMiningModal(false)}
        onSuccessClose={() => setSuccessModal(false)}
        onMining={() => {
          setOpenInvestModal(false);
          setMiningModal(true)
        }}
        tokenSymbol={tokenSymbol}
        modalOpen={openInvestModal}
        onModalClose={() => setOpenInvestModal(false)}
        onSuccess={() => {
          setMiningModal(false);
          setSuccessModal(true);
        }}
      />
      <SuccessModal
        modalOpen={miningModal}
        setModalOpen={() => setMiningModal(false)}
        title={"Tx submitted! Your transaction is being mined on the blockchain"}
      />
      <SuccessModal
        modalOpen={successModal}
        setModalOpen={() => setSuccessModal(false)}
        title={"Congratulations! You have successfully contributed in the Scallop sale."}
      />
      <div>
        <div className={"m-b-16"}>
          <AmountRaised tokenSymbol={tokenSymbol} />
          <div className="m-b-4">
            <PercentCompleted tokenSymbol={tokenSymbol} />
          </div>
          <TokenAllocationNumbersInToken tokenSymbol={tokenSymbol} />
        </div>
        <TotalRaised tokenSymbol={tokenSymbol} />
        <TokenPrice isDataField={true} tokenSymbol={tokenSymbol} />
        <Divider />
        <MinAndMaxAllocation tokenSymbol={tokenSymbol} />
        <TotalAllocation tokenSymbol={tokenSymbol} />
        <div className="m-t-32">
          {!isWhitelisted.value
            ? (
              <IsNotWhitelistedStatic />
            )
            : (
              <Button
                disabled={availableTokens.value.lte(0)}
                onClick={() => setOpenInvestModal(true)}
              >
                {
                  myAllocation.value.gt(0) ? "Invest More" : "Invest Now"
                }
              </Button>
            )
          }
        </div>
      </div>
    </div>
  );
}

export default Started;
