import styled from 'styled-components';
import { useWallet } from "use-wallet";
import { useLocation } from "react-router-dom";
import { useMediaQuery } from "react-responsive";
import detectEthereumProvider from '@metamask/detect-provider';
import React, { useCallback, useEffect, useState } from 'react';

import IconLoader from "../IconLoader";
import { Mixpanel } from "../../analytics/Mixpanel";

import Button from "../Button";
import AlertSnackbar from "../AlertSnackbar";
import MobileNav from './components/MobileNav';
import AccountButton from './components/AccountButton';

import useCore from '../../hooks/useCore';
import useGaTracker from "../../analytics/useGATracker";
import MobileTransactionInfo from "./components/modal/Transaction/MobileTransactionInfo";
import DesktopTransactionInfo from "./components/modal/Transaction/DesktopTransactionInfo";

import mobileLogo from '../../assets/images/MAHAStartersm.png';
import DesktopLogo from '../../assets/images/MAHAStarterlg.png';

const TopBar: React.FC = () => {
  useGaTracker();

  const core = useCore();
  const { account, connect } = useWallet();
  const location = useLocation();

  const isMobile = useMediaQuery({ maxWidth: '600px' });

  const [showTxModal, setShowTxModal] = useState<boolean>(false);
  const [showMobileMenu, toggleMobileMenu] = useState(false);
  const [showWarning, setShowWarning] = React.useState<boolean>(false);

  // For Wrong network
  const processNetwork = useCallback(async () => {
    const provider: any = await detectEthereumProvider();

    if (provider) {
      const chainId = Number(await provider.request({ method: 'eth_chainId' }));
      setShowWarning(chainId !== core.config.chainId);
    }
  }, [core]);


  // For Analytics
  useEffect(() => {
    Mixpanel.track(`ScreenView:${location.pathname}`);
  }, [location]);

  // For user tracking and Autowallet connect
  useEffect(() => {
    // const shouldBeDisconnected = localStorage.getItem('disconnectWallet');
    // if (!shouldBeDisconnected && !!!account) connect('injected');

    if (account) {
      Mixpanel.identify(account);
      Mixpanel.people.set({ walletId: account });
    }

    processNetwork();
  }, [account, core, connect, processNetwork]);

  return (
    <TopBarContainer>
      {
        isMobile
          ? <MobileTransactionInfo openModal={showTxModal} onDismiss={() => setShowTxModal(false)} />
          : <DesktopTransactionInfo openModal={showTxModal} onDismiss={() => setShowTxModal(false)} />
      }
      {/*<AlertSnackbar
        open={showWarning}
        title={'Wrong Network!'}
        subTitle={`You are on the wrong network, switch/add ${core.config.networkName} Network to use the app.`}
      />*/}
      <StyledTopBar>
        <StyledTopBarInner>
          <HideonPhone>
            <div className="single-line-center-between">
              <div className="dialog-class pointer" onClick={() => window.location.href = "/#/"}>
                <img
                  src={DesktopLogo}
                  width={269}
                  height={44}
                  alt={"logo"}
                />
              </div>
              <div className="single-line-center-start">
                <div className={"m-r-12"}>
                  <Button
                    tracking_id={"stake_maha_header"}
                    onClick={() =>
                      window.open('https://gov.mahadao.com/#/locker')}
                  >Stake MAHA
                  </Button>
                </div>
                {
                  !!account &&
                  <IconLoader
                    iconName={'Transaction'}
                    className={'pointer m-r-12'}
                    onClick={() => setShowTxModal(true)}
                  />
                }
                {/*<AccountButton showWarning={showWarning} />*/}
              </div>
            </div>
          </HideonPhone>
          <HideOnBigScreen>
            <div className="single-line-center-between">
             {/* <IconLoader
                iconName={!showMobileMenu ? 'Menu' : 'Cross'}
                onClick={() => toggleMobileMenu(!showMobileMenu)}
                className={'pointer'}
              />*/}
              <div/>

              <div onClick={() => window.location.href = "/#/"} className="pointer">
                <img
                  src={mobileLogo}
                  width={180}
                  height={44}
                  alt={"logo"}
                />
              </div>
              <div/>


              {
                !!account && (
                  <IconLoader
                    iconName={'Transaction'}
                    className={'pointer'}
                    onClick={() => setShowTxModal(true)}
                  />
                )
              }
            </div>
          </HideOnBigScreen>
          {/*<HideOnBigScreen>
            <MobileNav
              openMenu={showMobileMenu}
              showWarning={showWarning}
              isMainnet={true}
              onClick={() => toggleMobileMenu(!showMobileMenu)}
              onWalletClick={() => { }}
            />
          </HideOnBigScreen>*/}
        </StyledTopBarInner>
      </StyledTopBar>
    </TopBarContainer>
  );
};

const TopBarContainer = styled.div`
  position: fixed;
  z-index: 100;
  display: flex;
  flex-direction: column;
  width: 100%;
  top: 0;
`;

const HideonPhone = styled.div`
  width: 100%;
  display: block;
  @media (max-width: 600px) {
    display: none;
  } ;
`;

const HideOnBigScreen = styled.div`
  width: 100%;
  display: none;
  @media (max-width: 600px) {
    display: block;
  } ;
`;

const StyledTopBar = styled.div`
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  background: rgba(0, 0, 0);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
`;

const StyledTopBarInner = styled.div`
  align-content: center;
  display: flex;
  height: 72px;
  justify-content: space-between;
  width: 100%;
  padding: 0 60px;
  flex-wrap: wrap;
  @media (max-width: 600px) {
    padding: 0 16px;
  }
`;

export default TopBar;
