import styled from "styled-components";
import Countdown from "react-countdown";
import { useEffect, useMemo, useState } from "react";
import makeUrls, { TCalendarEvent } from "add-event-to-calendar";

import theme from "../../../theme";
import google from '../.././../assets/images/googleCalendar.svg'

import TextWrapper from "../../../components/TextWrapper";

import {
  IProjectIdProp,
  IStatusProps,
  ITokenSymbolProp,
  StartAndEndDate
} from "../../../utils/interface";

interface IStartAndEndDate {
  startAndEndDate: StartAndEndDate;
}

const IDOCountdown = (props: ITokenSymbolProp & IStatusProps & IProjectIdProp & IStartAndEndDate) => {
  const { tokenSymbol, id, status, startAndEndDate } = props;

  const [calendarLink, setLink] = useState('');

  useEffect(() => {
    const onClick = () => {
      let event: TCalendarEvent = {
        name: `Scallop IDO`,
        location: `https://${window.location.hostname}/#/projects/${id.toLowerCase()}`,
        details: `Reminder for ${id.toUpperCase()} IDO`,
        startsAt: new Date('October 27, 2021 3:00:00').toString(),
        endsAt: new Date('October 28, 2021 3:00:00').toString(),
      };
      setLink(makeUrls(event).google);
    };
    onClick();
  }, [startAndEndDate, id, tokenSymbol]);

  if (status === "closed") return <></>;

  return (
    <div className="material-primary m-b-24">
      <TextWrapper
        text={status === "upcoming" ? "IDO LAUNCHES IN" : "IDO ENDS IN"}
        fontSize={16}
        Fcolor={theme.color.transparent[100]}
        align={"center"}
        className="m-b-8"
      />
      <div className="single-line-center-center m-b-16">
        <Countdown
          date={
            new Date('October 27, 2021 3:00:00')
            /*status === "upcoming"
              ? startAndEndDate.startDate * 1000
              : startAndEndDate.endDate * 1000*/
          }
          renderer={({ days, hours, minutes, seconds, completed }) => {
            return (
              <div className="single-line-center-between">
                <div className="m-r-12">
                  <NumberContainer>
                    <TextWrapper
                      text={String(days)}
                      fontSize={18}
                      fontWeight={600}
                    />
                  </NumberContainer>
                  <TextWrapper
                    text={"Days"}
                    fontSize={12}
                    Fcolor={theme.color.transparent[100]}
                    align={"center"}
                  />
                </div>
                <div style={{ marginBottom: "22px" }}>
                  <TextWrapper
                    text={":"}
                    fontSize={18}
                    fontWeight={600}
                    className="m-r-12"
                  />
                </div>
                <div className="m-r-12">
                  <NumberContainer>
                    <TextWrapper
                      text={String(hours)}
                      fontSize={18}
                      fontWeight={600}
                    />
                  </NumberContainer>
                  <TextWrapper
                    text={"Hours"}
                    fontSize={12}
                    Fcolor={theme.color.transparent[100]}
                    align={"center"}
                  />
                </div>
                <div style={{ marginBottom: "22px" }}>
                  <TextWrapper
                    text={":"}
                    fontSize={18}
                    fontWeight={600}
                    className="m-r-12"
                  />
                </div>
                <div className="m-r-12">
                  <NumberContainer>
                    <TextWrapper
                      text={String(minutes)}
                      fontSize={18}
                      fontWeight={600}
                    />
                  </NumberContainer>
                  <TextWrapper
                    text={"Minutes"}
                    fontSize={12}
                    Fcolor={theme.color.transparent[100]}
                    align={"center"}
                  />
                </div>
                <div style={{ marginBottom: "22px" }}>
                  <TextWrapper
                    text={":"}
                    fontSize={18}
                    fontWeight={600}
                    className="m-r-12"
                  />
                </div>
                <div>
                  <NumberContainer>
                    <TextWrapper
                      text={String(seconds)}
                      fontSize={18}
                      fontWeight={600}
                    />
                  </NumberContainer>
                  <TextWrapper
                    text={"Seconds"}
                    fontSize={12}
                    Fcolor={theme.color.transparent[100]}
                    align={"center"}
                  />
                </div>
              </div>
            )
          }}
        />
      </div>
      {
        calendarLink && status === "upcoming" && (
          <HeaderButton onClick={() => window.open(calendarLink, '_blank')}>
            <img src={google} alt="calendar" height={24} />
            <span style={{ marginLeft: 8 }}>Add to Calendar</span>
          </HeaderButton>
        )
      }
    </div>
  );
}

export default IDOCountdown;

const NumberContainer = styled.div`
  background: rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 4px;
`;

const HeaderButton = styled.div`
  background: rgba(97, 134, 242, 0.32);
  border-radius: 8px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.88);
  display: flex;
  flex-direction: row;
  padding: 15px;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
`;
