import { useMemo } from "react";

import theme from "../../theme";

import DataField from "../DataField";

import useCore from "../../hooks/useCore";
import { ITokenSymbolProp } from "../../utils/interface";
import { getDisplayBalanceToken } from "../../utils/formatBalance";
import useGetMyAllocations from "../../hooks/state/useGetMyAllocations";

const TotalAllocation = (props: ITokenSymbolProp) => {
  const { tokenSymbol } = props;

  const core = useCore();
  const myAllocation = useGetMyAllocations(tokenSymbol);

  const yourAllocationsFormatted = useMemo(() => {
    return Number(getDisplayBalanceToken(myAllocation.value, core.tokens[tokenSymbol], 3))
      .toLocaleString('en-US', { minimumFractionDigits: 2, maximumFractionDigits: 2 })
  }, [myAllocation, core, tokenSymbol]);

  return (
    <div>
      <DataField
        label={"Your allocations"}
        value={`${yourAllocationsFormatted} ${tokenSymbol}`}
        isValueLoading={myAllocation.isLoading}
        labelFontSize={18}
        labelFontColor={theme.color.transparent[100]}
        valueFontSize={18}
        valueFontColor={"white"}
        valueFontWeight={600}
        className={"m-b-16"}
      />
    </div>
  );
}

export default TotalAllocation;
