import { Provider } from "react-redux";
import React, { useEffect } from "react";
import { SnackbarProvider } from "notistack";
import { UseWalletProvider } from "use-wallet";
import { useMediaQuery } from "react-responsive";
import { HashRouter as Router } from "react-router-dom";

import "./index.css";

import Navigation from "./Navigation";
import TopBar from "./components/TopBar";
import Popups from "./components/Popups";
import ModalsProvider from "./context/Modals";
import NoMetamaskNotice from "./components/NoMetamaskNotice";

import store from "./state";
import config from "./config";
import useCore from "./hooks/useCore";
import Updaters from "./state/Updaters";
import { noOp } from "./utils/constants";
import ProtocolProvider from "./context/Provider";

if (process.env.REACT_APP_MODE === 'production' || process.env.REACT_APP_MODE === 'test') {
  console.log = noOp;
  console.warn = noOp;
  console.error = noOp;
  console.info = noOp;
}

const Providers: React.FC = ({ children }) => {
  return (
    <UseWalletProvider
      chainId={config.chainId}
      connectors={{ injected: {} }}
    >
      <Provider store={store}>
        <Updaters />
        <ProtocolProvider>
          <ModalsProvider>
            <SnackbarProvider
              anchorOrigin={{ vertical: "top", horizontal: "right" }}
              maxSnack={2}
            >
              <>
                <Popups />
                {children}
              </>
            </SnackbarProvider>
          </ModalsProvider>
        </ProtocolProvider>
      </Provider>
    </UseWalletProvider>
  );
};

export let isMobileGlobal = false;

const App: React.FC = () => {
  const core = useCore()
  const isMobile = useMediaQuery({ maxWidth: "600px" });
  isMobileGlobal = isMobile;

  // @ts-ignore
  const { ethereum }: { ethereum: any } = window;

  useEffect(() => {
    if (ethereum)
      ethereum.on("chainChanged", () => {
        window.location.reload();
      });
  }, [ethereum]);

  if (!ethereum) return <NoMetamaskNotice />;
  if (!core) return <div />;

  return (
    <Providers>
      <Router>
        <TopBar />
        <Navigation />
      </Router>
    </Providers>
  );
};

export default App;

